// @mui
import { useEffect, useState } from 'react';
import { alpha } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
// components
import { useSettingsContext } from 'src/components/settings';
import { DatePicker } from '@mui/x-date-pickers';
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, Grid, InputAdornment, InputLabel, Menu, MenuItem, Modal, Select, TextField } from '@mui/material';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import { color, decryptToken, generateOfferCode, states } from 'src/utils/common';
import axios, { endpoints } from 'src/utils/axios';
import { constants } from 'src/utils/constant';
import { useSnackbar } from 'src/components/snackbar';
import DataTable from 'react-data-table-component';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';

import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs from 'dayjs';
import { useLocation, useNavigate } from 'react-router';
import CustomBreadcrumbs from 'src/components/custom-breadcrumbs/custom-breadcrumbs';
import { paths } from 'src/routes/paths';

const initialEmployeeData = {
    id: '',
    code: '',
    name: '',
    mobileNumber: '',
    dateOfBirth: null,
    address: {
        line1: '',
        line2: '',
        city: '',
        state: '',
        pincode: '',
    },
};

const initialCustomerErr = {
    name: false,
    email: false,
    mobileNumber: false,
    type: false,
    company: {
        name: false,
        gstNumber: false,
        billingAddress: {
            line1: false,
            line2: false,
            city: false,
            state: false,
            pincode: false,
        },
        shippingAddress: {
            line1: false,
            line2: false,
            city: false,
            state: false,
            pincode: false,
        },
    },
};

// ----------------------------------------------------------------------

export default function EmployeeDetailsView() {
    const settings = useSettingsContext();
    const { enqueueSnackbar } = useSnackbar();
    const navigate = useNavigate();
    // For Modal Open
    const [open, setOpen] = useState(false);

    const [employeesLoading, setEmployeesLoading] = useState(false);
    const [employees, setEmployees] = useState([]);
    const [employee, setEmployee] = useState(initialEmployeeData);
    const [editUser, setEditUser] = useState(false);

    // Temporary State
    const [tempData, setTempData] = useState({});

    const [searchQuery, setSearchQuery] = useState('');
    const location = useLocation();
    const employeeData = location?.state;
    employeeData.dateOfBirth = dayjs(employeeData.dateOfBirth);

    // For Actions Dropdown
    const [anchorEl, setAnchorEl] = useState(null);
    const openMenu = Boolean(anchorEl);


    // For Delete Employee
    const [openDialog, setOpenDialog] = useState(false);

    const handleOpen = () => {
        const employeeCode = generateOfferCode();
        setEmployee((prev) => ({ ...prev, code: employeeCode }));
        setOpen(true);
        setEditUser(false);
    };

    const handleClose = () => setOpen(false);


    const fetchData = async () => {

        try {
            const decryptedToken = localStorage.getItem(constants.keyUserToken);
            const accessToken = decryptToken(decryptedToken);
            const response = await axios.post(endpoints.employee.list, {}, {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            });
            if (response) {
                setEmployeesLoading(false);
                setEmployees(response.data.data);
            }
        } catch (err) {
            enqueueSnackbar('Failed to fetch employees!', { variant: 'error' });
            setEmployeesLoading(false);
        }
    };

    useEffect(() => {
        setEmployeesLoading(true);
        fetchData();

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    // You can use setFormData to update the state when user input changes
    const handleChange = (e) => {
        const { name, value } = e.target;
        setEmployee((prevFormData) => ({
            ...prevFormData,
            [name]: value,
        }));

    };


    const handleSaveEmployee = async () => {

        // If no errors, proceed with saving
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);

        // Create a new object without the id property
        const { id, ...data } = employee;

        axios.post(endpoints.employee.add, data,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Employee created successfully!', { variant: 'success' });
                // fetchData();
                handleClose();
                // Reset the employee  state
                setEmployee(initialEmployeeData);

                fetchData();
            }).catch((err) => {
                enqueueSnackbar(err.error || err.msg, { variant: 'error' });
            })
    };

    const handleEditDetails = () => {
        setEditUser(true);
        setAnchorEl(null);
        const employeeDt = {
            id: tempData._id,
            name: tempData.name,
            code: tempData.code,
            dateOfBirth: dayjs(tempData.dateOfBirth),
            mobileNumber: tempData.mobileNumber.slice(3),
            address: {
                line1: tempData.address.line1,
                line2: tempData.address.line2,
                city: tempData.address.city,
                state: tempData.address.state,
                pincode: tempData.address.pincode,
            },
        };
        setEmployee(employeeDt);
        setOpen(true);

    };

    const handleDeleteDetails = () => {
        setEmployee((prev) => ({ ...prev, id: tempData._id }));
        setOpenDialog(true);
        setAnchorEl(null);
    };

    const handleConfirmDelete = () => {

        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        const config = {
            headers: {
                Authorization: `Bearer ${accessToken}`
            }
        };
        axios.post(endpoints.employee.delete, { id: employee.id }, config)
            .then((res) => {
                if (res) {
                    enqueueSnackbar('Employee deleted successfully!', { variant: 'success' });
                    setOpenDialog(false);
                    setEmployee(initialEmployeeData);
                    fetchData();
                };
            }).catch((err) => {
                enqueueSnackbar(err.msg || err.error, { variant: 'error' });
            })
    };

    const handleUpdateEmployee = async () => {
        const decryptedToken = localStorage.getItem(constants.keyUserToken);
        const accessToken = decryptToken(decryptedToken);
        axios.post(endpoints.employee.update, employee,
            {
                headers: {
                    Authorization: `Bearer ${accessToken}`,
                }
            })
            .then((res) => {
                enqueueSnackbar('Employee updated successfully!', { variant: 'success' });
                // fetchData();
                handleClose();
                // Reset the employee  state
                setEmployee(initialEmployeeData);
                fetchData();
            }).catch((err) => {
                enqueueSnackbar(err.error || err.msg, { variant: 'error' });
            })



    };

    const employeeColumns = [
        {
            name: 'Name',
            selector: row => row.name,
            sortable: true,
            cell: row => (
                <Typography
                    onClick={(rowData) => {
                        navigate('/employee-details', { employee: rowData });
                    }}
                    style={{ cursor: 'pointer' }} // Change cursor to pointer to indicate clickable
                >
                    {row.name}
                </Typography>
            ),
        },
        {
            name: 'Employee Code',
            selector: row => row.code,
            sortable: true,
        },
        {
            name: 'Contact Number',
            selector: row => row.mobileNumber,
            sortable: true,
        },
        {
            name: 'Actions',
            cell: (row) => (
                <>
                    <Button onClick={(event) => {
                        setAnchorEl(event.currentTarget);
                        setTempData(row)
                    }}
                        style={{ color: color.actionButton }} variant='outlined' endIcon={<ArrowDropDownIcon />}>Action</Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={openMenu}
                        onClose={() => {
                            setAnchorEl(null);
                            setTempData({});
                        }}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={() => handleEditDetails()}>Edit Details</MenuItem>
                        <MenuItem onClick={() => handleDeleteDetails()}>Remove</MenuItem>
                    </Menu>
                </>
            ),
        },
    ];

    const filteredData = employees.filter((empData) =>
        empData?.name?.toLowerCase().includes(searchQuery?.toLowerCase())
        ||
        empData?.code?.toLowerCase().includes(searchQuery?.toLowerCase())
    );


    const handleAddressChange = (e) => {
        const { name, value } = e.target;
        // Restricting pincode to 6 digits
        if (name === 'pincode' && value.length > 6) {
            return;
        }
        setEmployee((prevFormData) => ({
            ...prevFormData,
            address: {
                ...prevFormData.address,
                [name]: value,
            },
        }));
    };


    return (
        <Container maxWidth={settings.themeStretch ? false : 'xl'}>
            <CustomBreadcrumbs
                heading='Employee Details'
                links={[
                    {
                        name: 'Dashboard',
                        href: paths.dashboard.root,
                    },
                    {
                        name: `${employeeData.name}`,
                        // href: paths.dashboard.general.offers,
                    },
                ]}
                sx={{
                    mb: { xs: 3, md: 5 },
                }}
            />

            <Box
                sx={{
                    mt: 2,
                    width: 1,
                    p: 2,
                    borderRadius: 2,
                    bgcolor: (theme) => alpha(theme.palette.grey[500], 0.04),
                    border: (theme) => `dashed 1px ${theme.palette.divider}`,
                }}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography variant='h6'>Basic Details</Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label='Name'
                            value={employeeData.name}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label='Employee Code'
                            value={employeeData.code}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            label='Mobile Number'
                            value={employeeData.mobileNumber}
                            fullWidth
                            disabled
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <LocalizationProvider dateAdapter={AdapterDayjs} >
                            <DatePicker
                                label="Date of Birth"
                                defaultValue={employeeData.dateOfBirth}
                                slotProps={{ textField: { fullWidth: true } }}
                                disabled
                            />
                        </LocalizationProvider>
                    </Grid>
                    <Grid item xs={8}>
                        <TextField
                            label='Address'
                            multiline
                            rows={2}
                            value={`${employeeData.address.line1}${employeeData.address.line2 ? `, ${employeeData.address.line2}` : ''}, ${employeeData.address.city}, ${employeeData.address.state} - ${employeeData.address.pincode}`}
                            fullWidth
                            disabled
                        />
                    </Grid>
                </Grid>
            </Box>
            <Box
                sx={{
                    mt: 2,
                    width: 1,
                    p: 2,
                    borderRadius: 2,
                    bgcolor: (theme) => alpha(theme.palette.grey[500], 0.04),
                    border: (theme) => `dashed 1px ${theme.palette.divider}`,
                }}
            >
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <Typography variant='h6'>Sales History</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <DataTable
                            columns={[]}
                            data={[]}
                        />
                    </Grid>

                </Grid>
            </Box>

            {/* Dialog For Delete */}
            <Dialog open={openDialog} onClose={() => setOpenDialog(false)}>
                <DialogTitle>Remove Employee</DialogTitle>
                <DialogContent>
                    Are you sure you want to delete this employee?
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setOpenDialog(false)}>Cancel</Button>
                    <Button onClick={handleConfirmDelete} variant="contained" color="error">
                        Delete
                    </Button>
                </DialogActions>
            </Dialog>

        </Container>
    );
}
